import React from 'react';
import styled from 'styled-components';

const StyledLogo = styled.div`
  transform: translateY(-1rem);

  img {
    width: 100%;
    display: block;
    transition: width 0.5s;
    max-width: var(--max-width);
  }
`;

const Pre = styled.pre`
  margin-top: 0;
  margin-bottom: 0;
  font-size: 1.25rem;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-image: linear-gradient(
    180deg,
    rgba(131, 116, 235, 1) 0%,
    rgba(155, 116, 235, 1) 25%,
    rgba(232, 116, 235, 1) 26%,
    rgba(234, 175, 144, 1) 50%,
    rgba(234, 178, 139, 1) 51%,
    rgba(236, 213, 85, 1) 74%,
    rgba(225, 215, 91, 1) 75%,
    rgba(116, 236, 149, 1) 100%
  );

  @media (max-width: 767.98px) {
    font-size: 2.25vw;
  }
`;

const ascii = `
                         __                                    ___
   ________  ____ ______/ /_      _______  ______        _____/ (_)
  / ___/ _ \\/ __ \`/ ___/ __/_____/ ___/ / / / __ \\______/ ___/ / /
 / /  /  __/ /_/ / /__/ /_/_____/ /  / /_/ / /_/ /_____/ /__/ / /
/_/   \\___/\\__,_/\\___/\\__/     /_/   \\__, /\\____/      \\___/_/_/
                                    /____/
`;

const Logo = () => (
  <StyledLogo>
    <Pre dangerouslySetInnerHTML={{ __html: ascii }} />
  </StyledLogo>
);

export default Logo;
